import CardDota from "@/components/utils/cards/Card"
import { membershipRecord, Memberships } from "@/constants"
import useAxios from "@/hooks/useAxios"
import { useRouter } from "next/router"

const Upgrade = () => {
    const { data: membership } = useAxios<{ membership: Memberships }>({ url: 'users/current-membership', method: 'get' })
    const sortedMemberships = Object.keys(membershipRecord) as Memberships[];

    const router = useRouter()
    const nextMembership = membership && membership.membership
        && sortedMemberships.find((m) => {
            return (
                membershipRecord[m]?.order > membershipRecord[membership.membership as Memberships]?.order
            );
        })

    return (
        <CardDota classNames=" !border-white !p-0 flex items-center  relative h-[250px]">
            <img
                src="/img/memberships/package-1.png"
                className="w-full object-cover rounded-lg h-full"
                alt=""
            />
            <div className="absolute left-10 text-black font-bold text-2xl top-10">
                <span>Membresía {membership && membershipRecord[membership.membership]?.display}</span>
            </div>
            <div className="absolute bottom-0 p-4 flex items-center  backdrop-blur-lg w-full rounded-b-lg">
                {nextMembership && membershipRecord[nextMembership] ? (
                    <div className="flex items-center justify-between w-full">
                        <span className="text-black w-80">
                            Actualiza tu membresia a {nextMembership && membershipRecord[nextMembership]?.display}
                        </span>
                        <div className="bg-blue-600 px-6 py-3 rounded-full cursor-pointer" onClick={() => router.push('purchases/membership')}>
                            <span>Upgrade</span>
                        </div>
                    </div>
                ) :
                    (
                        <span className="text-black">Ya cuentas con la membresia mas alta</span>
                    )}
            </div>
        </CardDota>
    )
}

export default Upgrade